<template>
  <div>
    <el-table
      stripe
      :border="border"
      :header-cell-style="tableHeader"
      :max-height="maxHeight"
      :show-header="showHeader"
      :data="data"
      :span-method="mergeColumns"
      :show-summary="showSummary"
      :sum-text="sumText"
      :summary-method="summaryMethod"
      :row-style="rowStyle"
      @selection-change="selectChange">
      <template v-for="column in columns">
        <!--操作列-->
        <el-table-column
          v-if="column.operation"
          header-align="center"
          :key="column.prop"
          :prop="column.prop"
          :width="column.width"
          :label="column.label"
          :align="column.align?column.align:'center'">
          <template slot-scope="scope">
            <el-button
              type="text"
              v-for="item in column.operations"
              :key="item.event"
              @click="emitEvent(item.event, scope.row[rowKey])">
              {{item.text}}
            </el-button>
          </template>
        </el-table-column>
        <!--状态列-->
        <el-table-column
          v-else-if="column.prop==='status'"
          header-align="center"
          :key="column.prop"
          :prop="column.prop"
          :width="column.width"
          :label="column.label"
          :align="column.align?column.align:'center'">
          <template slot-scope="scope">
            <el-tag
              :type="scope.row.statusType">
              {{scope.row[column.prop]}}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column
          v-else-if="['selection', 'index', 'expand'].includes(column.type)"
          header-align="center"
          :type="column.type"
          :key="column.prop"
          :prop="column.prop"
          :width="column.width"
          :label="column.label"
          :align="column.align?column.align:'center'">
        </el-table-column>
        <!--普通列-->
        <el-table-column
          v-else
          header-align="center"
          :key="column.prop"
          :prop="column.prop"
          :min-width="column.width"
          :label="column.label"
          :align="column.align?column.align:'center'">
          <template v-if="column.children">
            <el-table-column
              v-for="subColumn in column.children"
              header-align="center"
              :key="subColumn.prop"
              :prop="subColumn.prop"
              :min-width="subColumn.width"
              :label="subColumn.label"
              :align="column.align?column.align:'center'">
              <template slot-scope="scope">
                <el-input
                  size="small"
                  v-if="subColumn.isEdit && subColumn.type === 'input'"
                  v-model="scope.row[subColumn.prop]"/>
                <el-select
                  size="small"
                  v-else-if="subColumn.isEdit && subColumn.type === 'select'"
                  v-model="scope.row[subColumn.prop]">
                  <el-option
                    v-for="option in subColumn.options"
                    :key="option.value"
                    :value="option.value"
                    :label="option.label">
                  </el-option>
                </el-select>
                <span v-else>{{scope.row[subColumn.prop]}}</span>
              </template>
            </el-table-column>
          </template>
          <template slot-scope="scope">
            <el-input
              size="small"
              v-if="column.isEdit && column.type === 'input'"
              v-model="scope.row[column.prop]"/>
            <el-select
              size="small"
              v-else-if="column.isEdit && column.type === 'select'"
              v-model="scope.row[column.prop]">
              <el-option
                v-for="option in column.options"
                :key="option.value"
                :value="option.value"
                :label="option.label">
              </el-option>
            </el-select>
            <span v-else>{{scope.row[column.prop]}}</span>
          </template>
        </el-table-column>
      </template>
    </el-table>
    <base-pagination
      v-if="pagination"
      :total="total"
      @dataChange="dataChange">
    </base-pagination>
  </div>
</template>

<script>
import {mapState} from 'vuex'
const BasePagination = () => import('./BasePagination')

export default {
  name: 'BaseTable',
  components: {BasePagination},
  props: {
    border: {
      type: Boolean,
      default: false
    },
    showHeader: {
      type: Boolean,
      default: true
    },
    maxHeight: {
      type: Number,
      default: 450
    },
    showSummary: {
      type: Boolean,
      default: false
    },
    sumText: {
      type: String,
      default: '合计'
    },
    columns: {
      type: Array,
      required: true
    },
    data: {
      type: Array,
      required: true
    },
    rowKey: {
      type: String,
      required: false
    },
    rowStyle: {
      type: Function
    },
    spanMethod: {
      type: Function
    },
    summaryMethod: {
      type: Function
    },
    mountedCallBack: {
      type: Function
    },
    pagination: {
      type: Boolean,
      default: false
    },
    total: {
      type: Number
    }
  },
  data () {
    return {}
  },
  computed: {
    ...mapState({
      tableHeader: state => state.app.tableHeader
    })
  },
  mounted () {
    const callBack = this.mountedCallBack
    if (typeof callBack === 'function') {
      this.$nextTick().then(() => {
        callBack()
      })
    }
  },
  methods: {
    emitEvent (event, id) {
      this.$emit(event, id)
    },
    mergeColumns ({row, column, rowIndex, columnIndex}) {
      const fn = this.spanMethod
      if (typeof fn === 'function') {
        return fn({
          row: row,
          column: column,
          rowIndex: rowIndex,
          columnIndex: columnIndex
        })
      }
    },
    summary ({ columns, data }) {
      const fn = this.summaryMethod
      if (typeof fn === 'function') {
        return fn({
          columns: columns,
          data: data
        })
      }
    },
    dataChange (size, page) {
      this.$emit('dataChange', size, page)
    },
    selectChange (selection) {
      const keys = selection.map(item => {
        return item[this.rowKey]
      })
      this.$emit('selectChange', keys)
    }
  }
}
</script>

<style scoped>
  /*.el-tag {*/
    /*width: 80px;*/
  /*}*/
</style>



